<template>
    <div class="background">
        <div style="height: 300px;width: 450px;background-color: white;border-radius: 8px;position:relative;left:550px;top:130px">
            <h3 style="color:#409EFF;padding-top: 10px">密码登陆</h3><br/>
            <el-input v-model="username" placeholder="请输入用户名" style="width: 400px"></el-input><br/><br/>
            <el-input placeholder="请输入密码" v-model="psw" show-password style="width: 400px"></el-input><br/><br/>
            <el-checkbox v-model="checked" style="margin-right: 305px">记住密码</el-checkbox><br/><br/>
            <el-button size="medium" type="primary" plain @click="login">登录</el-button>
        </div>
    </div>
</template>

<script>
    export default {
        name: "Login",
        data(){
            return {
                username: '',
                psw: '',
                checked: false
            }
        },
        methods:{
            login(){
                this.$axios.post('/login',{
                    username:this.username,
                    psw:this.psw
                })
                    .then(response=>{
                        if (response.data.data=='ok') {
                            this.$router.push({path:'/'});
                        }
                    })
                    .catch(function (error) {console.log(error);});
            },

        }
    }
</script>

<style scoped>
    .background{
        background-image: url(../assets/login_background.jpg);
        background-size: 100% 100%;
        position:absolute;
        width: 100%;
        height: 100%;
        min-width:1280px;
    }
</style>